import { FormControl, MenuItem, Box, Select, SelectChangeEvent, InputLabel } from '@mui/material';
import { FC } from 'react';
import { useDispatch } from 'react-redux';
import { setCurrentPage, setSortBy } from 'redux/movies/moviesSlice';
import { selectSortType } from 'redux/movies/selectors';
import { useTypedSelector } from 'hooks/redux';
import { SORT_TYPE } from 'utils/consts';

const SortSelect: FC = () => {
  const dispatch = useDispatch();
  const sortType = useTypedSelector(selectSortType);

  const handleChange = (event: SelectChangeEvent) => {
    dispatch(setSortBy(event.target.value));
    dispatch(setCurrentPage(1));
  };
  return (
    <Box sx={{ minWidth: 120, mb: '25px' }}>
      <FormControl
        fullWidth
        size="small"
      >
        <InputLabel> :</InputLabel>
        <Select
          value={sortType}
          label=" :"
          onChange={handleChange}
        >
          <MenuItem value={SORT_TYPE.POPULAR_DESC}>  </MenuItem>
          <MenuItem value={SORT_TYPE.POPULAR_ASC}>  </MenuItem>
          <MenuItem value={SORT_TYPE.RATING_DESC}>  </MenuItem>
          <MenuItem value={SORT_TYPE.RATING_ASC}>  </MenuItem>
        </Select>
      </FormControl>
    </Box>
  );
};

export default SortSelect;
